import React, { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  // CardFooter,
  Button,
  // Input,
  // FormGroup,
  // Label,
} from "reactstrap";
import "react-toastify/dist/ReactToastify.css";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";
import Classes from "./product.module.css";
import Table from "reactstrap/lib/Table";
import { NavLink } from "react-router-dom";
import ShowmodalAttr from "./ShowmodalAttr";
import ShowmodalSeller from "./ShowmodalSeller";

const Product = () => {
  const [showAttr, setShowAttr] = useState(false);
  const [improve, setImprove] = useState(false);
  const [EditStatus, setEditStatus] = useState(false);

  const [productname, setProductname] = useState("");
  const [productID, setProductID] = useState("");
  const [productIDSeller, setProductIDSeller] = useState("");
  // const [maincat, setMaincat] = useState("");
  const [seller, setSeller] = useState("");
  const [waranty, setWaranty] = useState("");
  const [color, setColor] = useState("");
  const [stock, setStock] = useState("");
  const [price, setPrice] = useState("");
  const [discount, setDiscount] = useState("");

  const [products, setProducts] = useState([]);
  const [attributes, setAttributes] = useState([]);
  const [Maincategory, setMaincategory] = useState([]);
  const [Allsellers, setAllsellers] = useState([]);
  const [AllWaranty, setAllWaranty] = useState([]);

  //edit attribute
  const toogleShowAttribute = (Attributes, name, id) => {
    if (!showAttr) {
      setShowAttr(true);
      setAttributes(Attributes);
      setProductname(name);
      setProductID(id);
    } else {
      setShowAttr(false);
      setAttributes([]);
      setProductname("");
      setProductID("");
    }
  };

  const toggleSeller = (name, id) => {
    if (!improve) {
      setImprove(true);
      setProductname(name);
      setProductIDSeller(id);
    } else {
      setImprove(false);
      setProductname("");
      setProductIDSeller("");
    }
  };

  const handleChangeColor = (e, id) => {
    const field = { ...attributes[id] };
    field.color = e.target.value;
    const newAttribute = [...attributes];
    newAttribute[id] = field;
    setAttributes(newAttribute);
  };

  const handleChangeStock = (e, index) => {
    const filed = { ...attributes[index] };
    filed.stock = e.target.value;
    const newAttribute = [...attributes];
    newAttribute[index] = filed;
    setAttributes(newAttribute);
  };

  const handleChangePrice = (e, index) => {
    const filed = { ...attributes[index] };
    filed.price = e.target.value;
    const newAttribute = [...attributes];
    newAttribute[index] = filed;
    setAttributes(newAttribute);
  };

  const handleChangeDiscount = (e, index) => {
    const filed = { ...attributes[index] };
    filed.discount = e.target.value;
    const newAttribute = [...attributes];
    newAttribute[index] = filed;
    setAttributes(newAttribute);
  };

  const handleDeleteField = (e, index, idAttribute, productID) => {
    const copyAttribute = [...attributes];
    copyAttribute.splice(index, 1);
    setAttributes(copyAttribute);

    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        mutation deleteProduct($atributeId:ID!,$productId:ID!){
          deleteProductAttribute(input:{atributeId:$atributeId,productId:$productId}){
            status,
            message
          }
        }
        `,
        variables: {
          productId: productID,
          atributeId: idAttribute,
        },
      },
    })
      .then((res) => {
        const { status, message } = res.data.data.deleteProductAttribute;
        if (status === 200) {
          toast.success(message);
          setEditStatus(!EditStatus);
        } else {
          toast.error(message);
        }
      })
      .catch((err) => console.log(err));
  };

  const submitEditAttribute = (e) => {
    let atributeId = [];
    const arrayHodlerAttribute = [];
    attributes.map((item) => {
      return arrayHodlerAttribute.push({
        seller: item.seller[0]._id,
        waranty: item.waranty[0]._id,
        color: item.color,
        price: parseInt(item.price),
        discount: parseInt(item.discount),
        stock: parseInt(item.stock),
      });
    });
    attributes.map((item) => {
      return atributeId.push(item._id);
    });

    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        mutation updateProductAttribute($productId:ID,$atributeId:[ID!]!,$attribute:[InputAttribute!]!) {
          updateProductAttribute(input:{productId:$productId,atributeId:$atributeId,attribute:$attribute}){
         status,
         message
       }
       }`,
        variables: {
          atributeId: atributeId,
          productId: null,
          attribute: arrayHodlerAttribute,
        },
      },
    })
      .then((res) => {
        const { status, message } = res.data.data.updateProductAttribute;
        if (status === 200) {
          toast.success(message);
          setEditStatus(!EditStatus);
        } else {
          toast.error(message);
        }
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
          query getAllProduct($page:Int,$limit:Int,$productId:ID){
               getAllProduct(page:$page,limit:$limit,productId:$productId){
                 _id,
                 fname,
                 ename,
                 category{
                   name
                 },
                 orginal,
                 description,
                 brand{
                   name
                 },
                 attribute{
                   _id,
                   stock,
                   color,
                   price,
                   discount,
                   seller{
                     _id,
                     name
                   },
                  waranty{
                    _id,
                     name
                   }
                 },
                 valuespecs{
                   value,
                   howspecs{
                     name,
                     specs{
                       name
                     }
                   }
                }
              }
             }
          
          `,
        variables: {
          page: 1,
          limit: 30,
          productId: null,
        },
      },
    })
      .then((res) => {
        if (res.data.errors) {
          toast.error("خطا در دریافت اطلاعات");
        } else {
          const { getAllProduct } = res.data.data;
          setProducts(getAllProduct);
        }
      })
      .catch((err) => console.log(err));
  }, [EditStatus]);

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name,
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: true,
          hasParent: false,
          catId: null,
        },
      },
    })
      .then((res) => {
        //main category
        const { getAllCategory } = res.data.data;
        setMaincategory(getAllCategory);
      })
      .catch((err) => console.log(err));
  }, []);

  //add seller
  const handleChangeMainCategory = (e) => {
    //all sellers
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
          query getAllSeller($catId:ID!){
              getAllSellers(catId:$catId){
                _id,
                name,
                label,
              }
            }
              `,
        variables: {
          catId: e.target.value,
        },
      },
    })
      .then((res) => {
        const { getAllSellers } = res.data.data;
        setAllsellers(getAllSellers);
      })
      .catch((err) => console.log(err));

    //all waranty
    //get all waranty
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
            query allWaranty($page:Int,$limit:Int){
              getAllWaranty(page:$page,limit:$limit){
                _id,
                name,
                label
              }
            }
                `,
        variables: {
          page: 1,
          limit: 30,
        },
      },
    })
      .then((res) => {
        if (res.data.errors) {
          const { message } = res.data.errors[0].data[0];
          toast.error(message);
        } else {
          const { getAllWaranty } = res.data.data;
          setAllWaranty(getAllWaranty);
        }
      })
      .catch((err) => console.log(err));
  };
  const handleChangeSeller = (e) => {
    setSeller(e.target.value);
  };
  const handleChangeWaranty = (e) => {
    setWaranty(e.target.value);
  };
  const handleChangeColorSeller = (e) => {
    setColor(e.target.value);
  };
  const handleChangeStockSeller = (e) => {
    setStock(e.target.value);
  };
  const handleChangePriceSeller = (e) => {
    setPrice(e.target.value);
  };
  const handleChangeDiscountSeller = (e) => {
    setDiscount(e.target.value);
  };

  const handleSubmitAddSeller = (productID) => {
    const arrayHodler = [];
    arrayHodler.push({
      color: color,
      price: parseInt(price),
      stock: parseInt(stock),
      seller: seller,
      waranty: waranty,
      discount: parseInt(discount),
    });

    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        mutation updateProductAttribute($productId:ID,$atributeId:[ID],$attribute:[InputAttribute!]!) {
          updateProductAttribute(input:{productId:$productId,atributeId:$atributeId,attribute:$attribute}){
         status,
         message
       }
       }`,
        variables: {
          productId: productID,
          atributeId: null,
          attribute: arrayHodler,
        },
      },
    })
      .then((res) => {
        const { status, message } = res.data.data.updateProductAttribute;
        if (status === 200) {
          toast.success(message);
          setEditStatus(!EditStatus);
        } else {
          toast.error(message);
        }
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xs="12">
          <Card>
            <CardHeader>لیست محصولات</CardHeader>
            <CardBody>
              {products.length > 0 ? (
                <Table responsive className={Classes.tabel}>
                  <thead>
                    <tr>
                      <th>نام محصول</th>
                      <th>عکس</th>
                      <th>برند</th>
                      <th>جرئیات فروشنده</th>
                      <th>امتیاز</th>
                      <th>عملیات</th>
                    </tr>
                  </thead>
                  <tbody>
                    {products.map((item, index) => {
                      const link = `/products/product/${item._id}`;
                      const picture = `/products/picture/${item._id}`;
                      return (
                        <tr key={item._id}>
                          <td>
                            <div className={Classes.td}>
                              <span>{item.fname}</span>
                              <span>{item.ename}</span>
                            </div>
                          </td>
                          <td>
                            <img
                              src={`${process.env.REACT_APP_BASE_URL}${item.orginal}`}
                              alt={item.orginal}
                            />
                          </td>
                          <td>{item.brand[0].name}</td>
                          <td>
                            <div className={Classes.Box}>
                              <Button
                                size="sm"
                                color="primary"
                                className="ml-3"
                                onClick={() =>
                                  toogleShowAttribute(
                                    item.attribute,
                                    item.fname,
                                    item._id
                                  )
                                }
                              >
                                <i className="fa fa-eye fa-lg"></i>
                              </Button>
                              <Button
                                size="sm"
                                color="success"
                                onClick={() =>
                                  toggleSeller(item.fname, item._id)
                                }
                              >
                                <i className="fa fa-plus fa-lg"></i>
                              </Button>
                            </div>
                          </td>
                          <td>
                            <span className="ml-3">0</span>
                          </td>
                          <td className={Classes.iconBox}>
                            <Button size="sm" color="primary" className="ml-3">
                              <NavLink to={link}>
                                <i className="fa fa-edit fa-lg"></i>
                              </NavLink>
                            </Button>
                            <Button size="sm" color="success" className="ml-3">
                              <NavLink to={picture}>
                                <i className="fa fa-file-image-o fa-lg"></i>
                              </NavLink>
                            </Button>
                            <Button size="sm" color="danger" className="ml-3">
                              <i className="fa fa-trash fa-lg"></i>
                            </Button>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </Table>
              ) : (
                <center>
                  <Spinner size="sm" />
                </center>
              )}
            </CardBody>
          </Card>
        </Col>
      </Row>

      {showAttr ? (
        <ShowmodalAttr
          success={showAttr}
          toggleSuccess={toogleShowAttribute}
          attributes={attributes}
          productname={productname}
          productID={productID}
          handleChangeColor={handleChangeColor}
          handleChangeStock={handleChangeStock}
          handleChangePrice={handleChangePrice}
          handleChangeDiscount={handleChangeDiscount}
          handleDeleteField={handleDeleteField}
          submitEditAttribute={submitEditAttribute}
        />
      ) : null}

      {improve ? (
        <ShowmodalSeller
          productname={productname}
          improve={improve}
          toggleSeller={toggleSeller}
          Maincategory={Maincategory}
          handleChangeMainCategory={handleChangeMainCategory}
          Allsellers={Allsellers}
          AllWaranty={AllWaranty}
          handleChangeWaranty={handleChangeWaranty}
          handleChangeSeller={handleChangeSeller}
          handleChangeColorSeller={handleChangeColorSeller}
          handleChangeStockSeller={handleChangeStockSeller}
          handleChangePriceSeller={handleChangePriceSeller}
          handleChangeDiscountSeller={handleChangeDiscountSeller}
          handleSubmitAddSeller={handleSubmitAddSeller}
          productIDSeller={productIDSeller}
        />
      ) : null}
    </div>
  );
};

export default Product;
