import {
  Col,
  Modal,
  ModalBody,
  ModalHeader,
  Row,
  FormGroup,
  Label,
  Input,
  Button,
  ModalFooter
} from "reactstrap";
import React from "react";
import Classes from "./product.module.css";


const Showmodal = (props) => {
  const {
    success,
    toggleSuccess,
    attributes,
    productname,
    handleChangeColor,
    handleChangeStock,
    handleChangePrice,
    handleChangeDiscount,
    handleDeleteField,
    submitEditAttribute,
    productID
  } = props;


  return (
    <Modal
      isOpen={success}
      toggle={toggleSuccess}
      className={"modal-success " + props.className}
    >
      <ModalHeader toggle={toggleSuccess}>{productname}</ModalHeader>
      <ModalBody>
        {attributes.length > 0
          ? attributes.map((item, index) => {
              return (
                <Row className="p-3" key={index}>
                  <FormGroup row>
                    <Col md="4">
                      <FormGroup>
                        <Label>فروشگاه</Label>
                        <Input value={item.seller[0].name} disabled />
                      </FormGroup>
                    </Col>
                    <Col md="4">
                      <FormGroup>
                        <Label>گارانتی</Label>
                        <Input value={item.waranty[0].name} disabled />
                      </FormGroup>
                    </Col>
                    <Col md="4">
                      <FormGroup>
                        <Label>رنگ</Label>
                        <Input
                          type="select"
                          onChange={(e) => handleChangeColor(e, index)}
                          value={item.color}
                        >
                          <option></option>
                          <option value="black">مشکی</option>
                          <option value="white">سفید</option>
                          <option value="red">قرمز</option>
                        </Input>
                      </FormGroup>
                    </Col>
                    <Col md="3">
                      <FormGroup>
                        <Label>تعداد</Label>
                        <Input
                          type="number"
                          value={item.stock}
                          onChange={(e) => handleChangeStock(e, index)}
                        />
                      </FormGroup>
                    </Col>
                    <Col md="3">
                      <FormGroup>
                        <Label>قیمت(به تومان)</Label>
                        <Input type="number" value={item.price}  onChange={(e) => handleChangePrice(e, index)} />
                      </FormGroup>
                    </Col>
                    <Col md="3">
                      <FormGroup>
                        <Label>درصد تخفیف</Label>
                        <Input type="number" value={item.discount} onChange={(e) => handleChangeDiscount(e, index)} />
                      </FormGroup>
                    </Col>
                    {/* <Col md="1">
                      <FormGroup>
                        <Label>ویژه</Label>
                        <div>
                          <i className="fa fa-star fa-lg"></i>
                        </div>
                      </FormGroup>
                    </Col> */}
                    <Col md="2">
                      <div className={Classes.Boxred} onClick={(e)=>handleDeleteField(e,index,item._id,productID)}>
                        <i className="fa fa-trash fa-lg"></i>
                      </div>
                    </Col>
                  </FormGroup>
                </Row>
              );
            })
          : null}
      </ModalBody>
      <ModalFooter>
        <Button color="primary" className="mr-3" onClick={(e)=>submitEditAttribute(e)} >ویرایش</Button>
        <Button color="danger" onClick={toggleSuccess } >لغو</Button>
      </ModalFooter>
    </Modal>
  );
};

export default Showmodal;
