import {
  Col,
  Modal,
  ModalBody,
  ModalHeader,
  FormGroup,
  Label,
  Input,
  Button,
  ModalFooter,
} from "reactstrap";
import React from "react";
import Classes from "./product.module.css";

const ShowmodalSeller = (props) => {
  const {
    improve,
    toggleSeller,
    productname,
    Maincategory,
    productIDSeller,
    handleChangeMainCategory,
    Allsellers,
    AllWaranty,
    handleChangeSeller,
    handleChangeWaranty,
    handleChangeColorSeller,
    handleChangeStockSeller,
    handleChangePriceSeller,
    handleChangeDiscountSeller,
    handleSubmitAddSeller
  } = props;

  return (
    <Modal
      isOpen={improve}
      toggle={toggleSeller}
      className={"modal-lg" + props.className}
    >
      <ModalHeader toggle={toggleSeller}>
        اضافه کردن فروشنده جدید - ({productname}){" "}
      </ModalHeader>
      <ModalBody>
        <div className={Classes.BoxValue}>
          <FormGroup row>
            <Col xs="4">
              <FormGroup row>
                <Label>دسته بندی اصلی</Label>
                <Input type="select" onChange={handleChangeMainCategory}>
                  <option></option>
                  {Maincategory.map((item, index) => (
                    <option value={item._id} key={index}>
                      {item.name}
                    </option>
                  ))}
                </Input>
              </FormGroup>
            </Col>
            <Col xs="4">
              <FormGroup row>
                <Label>فروشندگان</Label>
                <Input type="select" onChange={handleChangeSeller}>
                  <option></option>
                  {Allsellers.map((item, index) => (
                    <option value={item._id} key={index}>
                      {item.name}
                    </option>
                  ))}
                </Input>
              </FormGroup>
            </Col>
            <Col xs="4">
              <FormGroup row>
                <Label>گارانتی</Label>
                <Input type="select" onChange={handleChangeWaranty}>
                  <option></option>
                  {AllWaranty.map((item, index) => (
                    <option value={item._id} key={index}>
                      {item.name}
                    </option>
                  ))}
                </Input>
              </FormGroup>
            </Col>
            <Col xs="3">
              <FormGroup row>
                <Label>رنگ ها</Label>
                <Input type="select" onChange={handleChangeColorSeller}>
                  <option></option>
                  <option value="red">قرمز</option>
                  <option value="blue">آبی</option>
                  <option value="black">مشکی</option>
                </Input>
              </FormGroup>
            </Col>
            <Col xs="3">
              <FormGroup row>
                <Label>تعداد</Label>
                <Input type="number" onChange={handleChangeStockSeller} />
              </FormGroup>
            </Col>
            <Col xs="3">
              <FormGroup row>
                <Label>قیمت</Label>
                <Input type="number" onChange={handleChangePriceSeller} />
              </FormGroup>
            </Col>
            <Col xs="3">
              <FormGroup row>
                <Label>تخفیف</Label>
                <Input type="number" onChange={handleChangeDiscountSeller} />
              </FormGroup>
            </Col>
          </FormGroup>
        </div>
      </ModalBody>
      <ModalFooter>
        <Button color="primary" className="mr-3" onClick={()=>handleSubmitAddSeller(productIDSeller)}>
          ثبت
        </Button>
        <Button color="danger" onClick={toggleSeller}>لغو</Button>
      </ModalFooter>
    </Modal>
  );
};

export default ShowmodalSeller;
